body {
    height: 100vh;
    width: 100vw;
    margin: 0px;
    overflow: hidden;

    background-color: rgb(84, 62, 105);

    display: grid;
    place-items: center;
}

#wrapper {
    aspect-ratio: 1.618;
    width: 90vmin;

    position: relative;
    z-index: 0;
}

#wrapper>.shape {
    height: calc(20% * 1.618);
    width: 20%;

    position: absolute;

    transition: left, top, border-radius, background-color;
    transition-duration: 1s, 1s, 200ms, 750ms;
    transition-timing-function: ease-in-out, ease-in-out, ease-in-out, ease-in-out, ease-in-out, ease-in-out;
}

/* CONFIGS */
/* SINGLE COLUMN */
#wrapper[data-configuration="0"] > .shape[data-order="0"] {
    left: 0%;
    top: calc(50% - (20% * 1.618) / 2);

    z-index: 5;
    background-color: #76C893;
}

#wrapper[data-configuration="0"] > .shape[data-order="1"] {
    left: 20%;
    top: calc(50% - (20% * 1.618) / 2);

    z-index: 4;
    background-color: #52B69A;
}

#wrapper[data-configuration="0"] > .shape[data-order="2"] {
    left: 40%;
    top: calc(50% - (20% * 1.618) / 2);

    z-index: 3;
    background-color: #34A0A4;
}

#wrapper[data-configuration="0"] > .shape[data-order="3"] {
    left: 60%;
    top: calc(50% - (20% * 1.618) / 2);

    z-index: 2;
    background-color: #168AAD;
}

#wrapper[data-configuration="0"] > .shape[data-order="4"] {
    left: 80%;
    top: calc(50% - (20% * 1.618) / 2);

    z-index: 1;
    background-color: #1A759F;
}

/* SLICES */
/* LEFT SLICE */
#wrapper[data-configuration="1"] > .shape[data-order="0"] {
    left: 0%;
    top: 0%;

    z-index: 5;
    background-color: #76C893;
}

#wrapper[data-configuration="1"] > .shape[data-order="2"] {
    left: 40%;
    top: 0%;

    z-index: 3;
    background-color: #34A0A4;
}

#wrapper[data-configuration="1"] > .shape[data-order="4"] {
    left: 80%;
    top: 0%;

    z-index: 1;
    background-color: #168AAD;
}

/* RIGHT SLICE */
#wrapper[data-configuration="1"] > .shape[data-order="1"] {
    left: 20%;
    top: calc(100% - (20% * 1.618));

    z-index: 4;
    background-color: #52B69A;
}

#wrapper[data-configuration="1"] > .shape[data-order="3"] {
    left: 60%;
    top: calc(100% - (20% * 1.618));

    z-index: 2;
    background-color: #1A759F;
}

/* SLICE MERGES */
/* LEFT SLICE */
#wrapper[data-configuration="2"] > .shape[data-order="0"] {
    left: 0%;
    top: 0%;

    z-index: 5;
    background-color: #76C893;
}

#wrapper[data-configuration="2"] > .shape[data-order="2"] {
    left: 20%;
    top: 0%;

    z-index: 4;
    background-color: #52B69A;
}

#wrapper[data-configuration="2"] > .shape[data-order="4"] {
    left: 40%;
    top: 0%;

    z-index: 3;
    background-color: #34A0A4;
}

/* RIGHT SLICE */
#wrapper[data-configuration="2"] > .shape[data-order="1"] {
    left: 60%;
    top: calc(100% - (20% * 1.618));

    z-index: 2;
    background-color: #168AAD;
}

#wrapper[data-configuration="2"] > .shape[data-order="3"] {
    left: 80%;
    top: calc(100% - (20% * 1.618));

    z-index: 1;
    background-color: #1A759F;
}

@media (hover: hover) {
    #wrapper .shape:hover {
        background-color: #e6e6e6 !important;
        border-radius: 50px;
    }
}